'use strict';
import axios from 'axios';

angular.module('song-aggregator.song')
  .service('songApi', songApi);

function songApi() {

  /**
   * calls Api Songs Aggregator Endpoint
   * @returns {AxiosPromise}
   */
  const searchSong = function searchSong() {
    return axios(
      {
        method: 'GET',
        uri: '/api/v1/songs',
        params: { title, artist },
      }
    );
  };

  return {
    searchSong
  };
}